.main-wrapper {
    max-width: @desktop-container2;
    margin: 0 auto;
}

.main {
    .__list {
        margin: 0 auto;
    }

    &__title--white {
        color: @white;
        margin-bottom: 40px;
    }

    &__title--center {
        width: 100%;
        text-align: center;
        margin-bottom: 20px;
    }

    &__subtitle {
        text-align: center;
        margin-bottom: 60px;
    }
}

@media (max-width: @mobile-width) {
    .main-wrapper {
        max-width: @mobile-container;
        margin: 0 auto;
    }

    .main {
        &__title--white {
            font-size: 30px;
            font-weight: 700;
            line-height: 30px;
            letter-spacing: -0.6px;
        }
        
        &__title--center {
            font-size: 30px;
            font-weight: 700;
            line-height: 30px;
            letter-spacing: -0.6px;
        }
        
        &__subtitle {
            font-size: 16px;
            font-weight: 500;
            line-height: 20px;
        }

        &__title--accent {
            display: block;
        }
    }
}