.tooltip {
    z-index: 100;
    position: absolute;

    &__toggle {
        cursor: pointer;
        border-radius: 50%;
        background-color: @primary;
        height: 30px;
        width: 30px;
        background-image: url("../img/dot.svg");
        background-position: center;
        background-repeat: no-repeat;

        &--one {
            transform: translate(17px,-373px);
        }
    
        &--two {
            transform: translate(124px,-138px);
        }

        &--three {
            transform: translate(53px,-62px);
        }

        &--four {
            transform: translate(186px,-446px);
        }
    }

    &__img {
        position: absolute;
        z-index: 80;
    }

    &__img--one {
        transform: translateX(-278px);
        visibility: hidden;
    }

    &__img--one.active {
        visibility: visible;
    }

    &__img--two {
        transform: translateX(-278px);
        visibility: hidden;
    }

    &__img--two.active {
        visibility: visible;
    }

    &__img--three {
        transform: translate(-278px,243px);
        visibility: hidden;
    }

    &__img--three.active {
        visibility: visible;
    }

    &__img--four {
        transform: translate(-278px, 0);
        visibility: hidden;
    }

    &__img--four.active {
        visibility: visible;
    }

    &__text {
        min-width: 220px;
        position: absolute;
        padding: 25px 30px;
        border-radius: 15px;
        background: #F3F5FB;
        display: none;
        position: absolute; 
        top: 0;
        left: 0;
        z-index: 100;
        display: none;
        filter: drop-shadow(0px 0px 3px #C7D0E7);

        &::after {
            position: absolute;
            content: "";
            width: 0;
            height: 0;
        }
    }

    &__text--one {
        transform: translate(-275px,-403px);
        text-align: right;

        &::after {
            top: 30%;
            border: 0 solid transparent;
            border-bottom-width: 12px;
            border-top-width: 12px;
            border-left: 12px solid @primary;
            transform: translateX(30px);
        }
    }

    &__text--two {
        transform: translate(-275px,-150px);
        text-align: right;

        &::after {
            top: 30%;
            border: 0 solid transparent;
            border-bottom-width: 12px;
            border-top-width: 12px;
            border-left: 12px solid @primary;
            transform: translateX(30px);
        }
    }

    &__text--three {
        transform: translate(320px,-100px);

        &::after {
            top: 30%;
            left: 0;
            border: 0 solid transparent;
            border-top-width: 12px;
            border-bottom-width: 12px;
            border-right: 12px solid @primary;
            transform: translateX(-15px);
        }
    }

    &__text--four {
        transform: translate(320px,-495px);

        &::after {
            top: 40%;
            left: 0;
            border: 0 solid transparent;
            border-top-width: 12px;
            border-bottom-width: 12px;
            border-right: 12px solid @primary;
            transform: translateX(-15px);
        }
    }
}

.tooltip__toggle:hover + .tooltip__text {
    display: block;
}

@media (max-width: @mobile-width) {
    .tooltip__toggle:focus + .tooltip__text {
        display: block;
    }

    .tooltip__text--one {
        transform: translate(13px,-318px);

        &::after {
            top: -17%;
            left: -17px;
            border-style: solid;
            border-width: 0 6px 12px 6px;
            border-color: transparent transparent @primary transparent;
        }
    }

    .tooltip__text--two {
        transform: translate(33px,-267px);

        &::after {
            top: 100%;
            left: 70px;
            width: 0;
            height: 0;
            border-style: solid;
            border-width: 12px 6px 0 6px;
            border-color: @primary transparent transparent transparent;
        }
    }

    .tooltip__text--three {
        transform: translate(33px,-194px);

        &::after {
            top: 100%;
            left: 43px;
            width: 0;
            height: 0;
            border-style: solid;
            border-width: 12px 6px 0 6px;
            border-color: @primary transparent transparent transparent;
        }
    }

    .tooltip__text--four {
        transform: translate(33px,-394px);

        &::after {
            top: -9%;
            left: 177px;
            border-style: solid;
            border-width: 0 6px 12px 6px;
            border-color: transparent transparent @primary transparent;
        }
    }
}