.card {
  position: sticky;
  top: 10px;
  padding: 140px 230px 137px 230px;
  border-radius: 60px;
  background-color: @white;
  margin-bottom: 20px;
  
  &__picture {
    display: flex;
    flex-shrink: 0;
  }

  &--info {
    display: flex;
    justify-content: space-between;
  }
  
  &--accent {
    background-color: @primary;
  }

  &__item {
    max-width: 621px;
    color: @black;
    padding-left: 18px;
    margin-bottom: 25px;
    position: relative;
  }

  &__item::before {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    transform: translateY(150%);
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: @black;
  }

  &__item--accent {
    color: @white;

    &::before {
        background-color: @white;
    }
  }

  &__text {
    padding-top: 68px;
  }

  &__cars {
    display: flex;
    justify-content: center;
    gap: 100px;
  }
}

.card:last-child {
    margin-bottom: 0;
}

@media (max-width: @mobile-width) {
    .card {
        padding: 50px 28px 50px 20px;
        border-radius: 15px;
        position: static;
        width: 350px;

        &__cars {
            flex-direction: column;
        }

        &--info {
            flex-direction: column;
        }

        &__item {
            font-size: 16px;
            font-weight: 500;
            line-height: 20px; 
        }

        &__text {
            padding-top: 0;
        }
    }
}