.header {
  margin-top: 10px;
  padding: 10px 10px 10px 25px;
  display: flex;
  align-items: center;
  border-radius: 15px;
  background-color: @white;

  &__picture {
    display: flex;
    flex-shrink: 0;
  }

  &__btn-wrap {
    margin-left: auto;
    display: flex;
    align-items: center;
    gap: 40px;
  }

  &__link {
    display: block;
  }
}

@media (max-width: @mobile-width) {
  .header__btn--span {
    display: none;
  }

  .header__btn.btn--primary {
    width: 45px;
    height: 45px;
    border-radius: 5px;
    background-color: rgba(0, 155, 255, 0.15);
    background-image: url("../img/phone.svg");
    background-repeat: no-repeat;
    background-position: center;
    padding: 11px 11px;
  }

  .header__phone.phone-text {
    font-size: 14px;
    font-weight: 700;
    line-height: 20px;
  }

  .header__btn-wrap {
    gap: 15px;
  }
}