.center-mobile {
  width: 350px;
  margin: 0 auto;
}
.center-tablet {
  width: 768px;
  margin: 0 auto;
}
.center-desktop {
  width: 1440px;
  margin: 0 auto;
}
.center-desktop2 {
  width: 1840px;
  margin: 0 auto;
}
.align-center {
  display: flex;
  align-items: center;
  justify-content: center;
}
.list-reset {
  padding-left: 0;
  margin: 0;
  list-style-type: none;
}
.btn-reset {
  padding: 0;
  background-color: transparent;
  border: none;
}
.link-reset {
  text-decoration: none;
}
.title {
  margin: 0;
  font-size: 68px;
  line-height: 65px;
  font-weight: 700;
  color: #2b2e3e;
  letter-spacing: -1.36px;
  font-family: "Gilroy", "Arial", sans-serif;
}
.span {
  color: #009bff;
}
.text {
  margin: 0;
  font-size: 24px;
  font-weight: 500;
  line-height: 35px;
  font-family: "Gilroy", "Arial", sans-serif;
}
.tooltip-text {
  font-size: 16px;
  font-weight: 600;
  line-height: 20px;
  font-family: "Gilroy", "Arial", sans-serif;
}
.btn {
  padding: 12px 40px;
  border-radius: 5px;
  font-size: 14px;
  font-weight: 700;
  line-height: 25px;
  font-family: "Gilroy", "Arial", sans-serif;
}
.btn-primary {
  color: #ffffff;
  background-color: #009bff;
}
.btn-secondary {
  color: #009bff;
  background-color: rgba(0, 155, 255, 0.2);
}
.btn-icon {
  font-size: 16px;
  font-weight: 400;
  line-height: 25px;
  color: #2b2e3e;
  background-color: none;
  padding-left: 55px;
  font-family: "Gilroy", "Arial", sans-serif;
  border: 1px dashed #BFC9E0;
}
.phone-text {
  font-size: 18px;
  font-weight: 700;
  line-height: 25px;
  font-family: "Gilroy", "Arial", sans-serif;
  color: #2b2e3e;
}
@font-face {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Gilroy-Regular.woff2") format("woff2"), url("../../fonts/Gilroy-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/Gilroy-Medium.woff2") format("woff2"), url("../../fonts/Gilroy-Medium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/Gilroy-SemiBold.woff2") format("woff2"), url("../../fonts/Gilroy-SemiBold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/Gilroy-Bold.woff2") format("woff2"), url("../../fonts/Gilroy-Bold.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
img {
  max-width: 100%;
  flex-shrink: 0;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.stop-scroll {
  overflow: hidden;
}
.page {
  height: 100%;
  display: flex;
  flex-direction: column;
  margin: 0;
  background-color: #F3F5FB;
}
.page__wrapper {
  margin: 0 auto;
  width: 100%;
}
.page__upper {
  width: 1440px;
  margin: 0 auto;
}
@media (max-width: 1550px) {
  .page__upper {
    width: 350px;
    margin: 0 auto;
  }
}
.header {
  margin-top: 10px;
  padding: 10px 10px 10px 25px;
  display: flex;
  align-items: center;
  border-radius: 15px;
  background-color: #ffffff;
}
.header__picture {
  display: flex;
  flex-shrink: 0;
}
.header__btn-wrap {
  margin-left: auto;
  display: flex;
  align-items: center;
  gap: 40px;
}
.header__link {
  display: block;
}
@media (max-width: 1550px) {
  .header__btn--span {
    display: none;
  }
  .header__btn.btn--primary {
    width: 45px;
    height: 45px;
    border-radius: 5px;
    background-color: rgba(0, 155, 255, 0.15);
    background-image: url("../img/phone.svg");
    background-repeat: no-repeat;
    background-position: center;
    padding: 11px 11px;
  }
  .header__phone.phone-text {
    font-size: 14px;
    font-weight: 700;
    line-height: 20px;
  }
  .header__btn-wrap {
    gap: 15px;
  }
}
.main-wrapper {
  max-width: 1840px;
  margin: 0 auto;
}
.main .__list {
  margin: 0 auto;
}
.main__title--white {
  color: #ffffff;
  margin-bottom: 40px;
}
.main__title--center {
  width: 100%;
  text-align: center;
  margin-bottom: 20px;
}
.main__subtitle {
  text-align: center;
  margin-bottom: 60px;
}
@media (max-width: 1550px) {
  .main-wrapper {
    max-width: 350px;
    margin: 0 auto;
  }
  .main__title--white {
    font-size: 30px;
    font-weight: 700;
    line-height: 30px;
    letter-spacing: -0.6px;
  }
  .main__title--center {
    font-size: 30px;
    font-weight: 700;
    line-height: 30px;
    letter-spacing: -0.6px;
  }
  .main__subtitle {
    font-size: 16px;
    font-weight: 500;
    line-height: 20px;
  }
  .main__title--accent {
    display: block;
  }
}
.hero {
  display: flex;
  align-items: flex-start;
  padding: 120px 0 256px;
}
.hero__image {
  position: absolute;
  transform: translateX(-345px);
}
.hero__text {
  padding-top: 50px;
}
.hero__picture {
  flex-shrink: 0;
  display: flex;
}
.hero__title {
  width: 820px;
  display: block;
  letter-spacing: -1.36px;
  margin-bottom: 30px;
}
.hero__subtitle {
  margin-bottom: 65px;
}
.hero__btns {
  display: flex;
  gap: 15px;
}
.hero__btn {
  padding: 15px 40px;
  font-size: 16px;
}
@media (max-width: 1550px) {
  .hero__title {
    width: auto;
    font-size: 30px;
    font-weight: 700;
    line-height: 30px;
    letter-spacing: -0.6px;
  }
  .hero__subtitle {
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 20px;
  }
  .hero__btns {
    flex-direction: column;
    gap: 10px;
  }
  .hero__btn {
    max-width: 350px;
  }
  .hero__image {
    position: static;
    transform: translate(0, 0);
  }
  .hero__picture {
    order: -1;
  }
  .hero {
    flex-direction: column;
    align-items: center;
    padding: 35px 0 80px;
  }
  .hero__title {
    margin-bottom: 15px;
  }
  .hero__subtitle {
    margin-bottom: 30px;
  }
  .hero__text {
    padding-top: 25px;
  }
}
.btn {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  cursor: pointer;
}
.btn--primary {
  color: #ffffff;
  background-color: #009bff;
}
.btn--primary:hover {
  color: #009bff;
  background-color: rgba(0, 155, 255, 0.2);
}
.btn--icon {
  font-size: 16px;
  font-weight: 400;
  line-height: 25px;
  color: #2b2e3e;
  background-color: none;
  padding-left: 55px;
  font-family: "Gilroy", "Arial", sans-serif;
  border: 1px dashed #BFC9E0;
  padding: 15px 25px 15px 55px;
  background-image: url("../img/phone.svg");
