  }
}
.card {
  position: sticky;
  top: 10px;
  padding: 140px 230px 137px 230px;
  border-radius: 60px;
  background-color: #ffffff;
  margin-bottom: 20px;
}
.card__picture {
  display: flex;
  flex-shrink: 0;
}
.card--info {
  display: flex;
  justify-content: space-between;
}
.card--accent {
  background-color: #009bff;
}
.card__item {
  max-width: 621px;
  color: #2b2e3e;
  padding-left: 18px;
  margin-bottom: 25px;
  position: relative;
}
.card__item::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  transform: translateY(150%);
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: #2b2e3e;
}
.card__item--accent {
  color: #ffffff;
}
.card__item--accent::before {
  background-color: #ffffff;
}
.card__text {
  padding-top: 68px;
}
.card__cars {
  display: flex;
  justify-content: center;
  gap: 100px;
}
.card:last-child {
  margin-bottom: 0;
}
@media (max-width: 1550px) {
  .card {
    padding: 50px 28px 50px 20px;
    border-radius: 15px;
    position: static;
    width: 350px;
  }
  .card__cars {
    flex-direction: column;
  }
  .card--info {
    flex-direction: column;
  }
  .card__item {
    font-size: 16px;
    font-weight: 500;
    line-height: 20px;
  }
  .card__text {
    padding-top: 0;
  }
}
.tooltip {
  z-index: 100;
  position: absolute;
}
.tooltip__toggle {
  cursor: pointer;
  border-radius: 50%;
  background-color: #009bff;
  height: 30px;
  width: 30px;
  background-image: url("../img/dot.svg");
  background-position: center;
  background-repeat: no-repeat;
}
.tooltip__toggle--one {
  transform: translate(17px, -373px);
}
.tooltip__toggle--two {
  transform: translate(124px, -138px);
}
.tooltip__toggle--three {
  transform: translate(53px, -62px);
}
.tooltip__toggle--four {
  transform: translate(186px, -446px);
}
.tooltip__img {
  position: absolute;
  z-index: 80;
}
.tooltip__img--one {
  transform: translateX(-278px);
  visibility: hidden;
}
.tooltip__img--one.active {
  visibility: visible;
}
.tooltip__img--two {
  transform: translateX(-278px);
  visibility: hidden;
}
.tooltip__img--two.active {
  visibility: visible;
}
.tooltip__img--three {
  transform: translate(-278px, 243px);
  visibility: hidden;
}
.tooltip__img--three.active {
  visibility: visible;
}
.tooltip__img--four {
  transform: translate(-278px, 0);
  visibility: hidden;
}
.tooltip__img--four.active {
  visibility: visible;
}
.tooltip__text {
  min-width: 220px;
  padding: 25px 30px;
  border-radius: 15px;
  background: #F3F5FB;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 100;
  display: none;
  filter: drop-shadow(0px 0px 3px #C7D0E7);
}
.tooltip__text::after {
  position: absolute;
  content: "";
  width: 0;
  height: 0;
}
.tooltip__text--one {
  transform: translate(-275px, -403px);
  text-align: right;
}
.tooltip__text--one::after {
  top: 30%;
  border: 0 solid transparent;
  border-bottom-width: 12px;
  border-top-width: 12px;
  border-left: 12px solid #009bff;
  transform: translateX(30px);
}
.tooltip__text--two {
  transform: translate(-275px, -150px);
  text-align: right;
}
.tooltip__text--two::after {
  top: 30%;
  border: 0 solid transparent;
  border-bottom-width: 12px;
  border-top-width: 12px;
  border-left: 12px solid #009bff;
  transform: translateX(30px);
}
.tooltip__text--three {
  transform: translate(320px, -100px);
}
.tooltip__text--three::after {
  top: 30%;
  left: 0;
  border: 0 solid transparent;
  border-top-width: 12px;
  border-bottom-width: 12px;
  border-right: 12px solid #009bff;
  transform: translateX(-15px);
}
.tooltip__text--four {
  transform: translate(320px, -495px);
}
.tooltip__text--four::after {
  top: 40%;
  left: 0;
  border: 0 solid transparent;
  border-top-width: 12px;
  border-bottom-width: 12px;
  border-right: 12px solid #009bff;
  transform: translateX(-15px);
}
.tooltip__toggle:hover + .tooltip__text {
  display: block;
}
@media (max-width: 1550px) {
  .tooltip__toggle:focus + .tooltip__text {
    display: block;
  }
  .tooltip__text--one {
    transform: translate(13px, -318px);
  }
  .tooltip__text--one::after {
    top: -17%;
    left: -17px;
    border-style: solid;
    border-width: 0 6px 12px 6px;
    border-color: transparent transparent #009bff transparent;
  }
  .tooltip__text--two {
    transform: translate(33px, -267px);
  }
  .tooltip__text--two::after {
    top: 100%;
    left: 70px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 12px 6px 0 6px;
    border-color: #009bff transparent transparent transparent;
  }
  .tooltip__text--three {
    transform: translate(33px, -194px);
  }
  .tooltip__text--three::after {
    top: 100%;
    left: 43px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 12px 6px 0 6px;
    border-color: #009bff transparent transparent transparent;
  }
  .tooltip__text--four {
    transform: translate(33px, -394px);
  }
  .tooltip__text--four::after {
    top: -9%;
    left: 177px;
    border-style: solid;
    border-width: 0 6px 12px 6px;
    border-color: transparent transparent #009bff transparent;
  }
}
.car__img__front {
  position: relative;
}
.car__img__back {
  position: relative;
}
.car__img--front {
  opacity: 0.5;
}
.car__img--back {
  opacity: 0.5;
}
.car__img--front.active {
  opacity: 1;
}
.car__img--back.active {
  opacity: 1;
}
.modal {
  padding: 55px 60px 65px;
  border-radius: 30px;
  width: auto;
}
.modal__title {
  text-align: center;
  font-size: 38px;
  font-style: normal;
  font-weight: 700;
  line-height: 40px;
  font-family: "Gilroy", "Arial", sans-serif;
}
.modal__subtitle {
  font-size: 16px;
  font-weight: 400;
  line-height: 25px;
  font-family: "Gilroy", "Arial", sans-serif;
}
.modal__form {
  margin: 0;
  padding: 0;
}
.modal__fields {
  margin: 0;
  padding: 0;
  border: none;
  display: flex;
  gap: 20px;
  margin-bottom: 20px;
}
.modal__input {
  flex-grow: 1;
  border: none;
  border-radius: 5px;
  background: #EDF1F8;
  padding: 20px 25px;
  color: rgba(43, 46, 62, 0.45);
  font-family: "Gilroy", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 20px;
}
.modal__comment {
  width: 100%;
  border: none;
  border-radius: 5px;
  background: #EDF1F8;
  padding: 20px 25px;
  color: rgba(43, 46, 62, 0.45);
  font-family: "Gilroy", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 20px;
  margin-bottom: 20px;
  outline: 0px none transparent;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;
  resize: none;
}
.modal__span {
  color: rgba(43, 46, 62, 0.45);
  font-family: Gilroy;
  font-size: 12px;
  font-weight: 500;
  line-height: 20px;
}
.modal__btn-wrap {
  display: flex;
  align-items: center;
  gap: 20px;
}
.modal__link {
  color: rgba(43, 46, 62, 0.45);
  text-decoration: underline;
  cursor: pointer;
}
.hystmodal__close {
  top: 20px;
  right: 20px;
  background-image: url("../img/close-btn.svg");
  width: 18px;
  height: 18px;
}
@media (max-width: 1550px) {
  .modal {
    padding: 20px 20px;
    border-radius: 15px;
    max-width: 350px;
  }
  .modal__title {
    font-size: 26px;
    font-weight: 700;
    line-height: 30px;
  }
  .modal__subtitle {
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
  }
  .modal__fields {
    flex-direction: column;
    gap: 10px;
    margin-bottom: 10px;
  }
  .modal__btn-wrap {
    flex-direction: column;
  }
  .modal__span {
    text-align: center;
  }
  .modal__input {
    padding: 18px 20px;
    font-size: 14px;
    font-weight: 500;
    line-height: 20px;
  }
  .modal__comment {
    padding: 18px 20px;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 20px;
  }
  .modal__btn {
    padding: 17px 50px 18px 50px;
    font-size: 14px;
    font-weight: 700;
    line-height: 20px;
    width: 100%;
  }
}
