.hero {
  display: flex;
  align-items: flex-start;
  padding: 120px 0 256px;
}

.hero__image {
  position: absolute;
  transform: translateX(-345px);
}

.hero__text {
  padding-top: 50px;
}

.hero__picture {
  flex-shrink: 0;
  display: flex;
}

.hero__title {
  width: 820px;
  display: block;
  letter-spacing: -1.36px;
  margin-bottom: 30px;
}

.hero__subtitle {
  margin-bottom: 65px;
}

.hero__btns {
  display: flex;
  gap: 15px;
}

.hero__btn {
  padding: 15px 40px;
  font-size: 16px;
}

@media (max-width: @mobile-width) {
  .hero__title {
    width: auto;
    font-size: 30px;
    font-weight: 700;
    line-height: 30px;
    letter-spacing: -0.6px;
  }

  .hero__subtitle {
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 20px;
  }

  .hero__btns {
    flex-direction: column;
    gap: 10px;
  }

  .hero__btn {
    max-width: 350px;
  }

  .hero__image {
    position: static;
    transform: translate(0, 0);
  }

  .hero__picture {
    order: -1;
  }

  .hero {
    flex-direction: column;
    align-items: center;
    padding: 35px 0 80px;

    &__title {
      margin-bottom: 15px;
    }

    &__subtitle {
      margin-bottom: 30px;
    }
  }

  .hero__text {
    padding-top: 25px;
  }
}