.modal {
    padding: 55px 60px 65px;
    border-radius: 30px;
    width: auto;

    &__title {
        text-align: center;
        font-size: 38px;
        font-style: normal;
        font-weight: 700;
        line-height: 40px;
        .font-gilroy();
    }

    &__subtitle {
        font-size: 16px;
        font-weight: 400;
        line-height: 25px;
        .font-gilroy();
    }

    &__form {
        margin: 0;
        padding: 0;
    }

    &__fields {
        margin: 0;
        padding: 0;
        border: none;
        display: flex;
        gap: 20px;
        margin-bottom: 20px;
    }

    &__input {
        flex-grow: 1;
        border: none;
        border-radius: 5px;
        background: #EDF1F8;
        padding: 20px 25px;
        color: rgba(43, 46, 62, 0.45);
        .font-gilroy();
        font-size: 16px;
        font-weight: 500;
        line-height: 20px;
    }

    &__comment {
        width: 100%;
        border: none;
        border-radius: 5px;
        background: #EDF1F8;
        padding: 20px 25px;
        color: rgba(43, 46, 62, 0.45);
        .font-gilroy();
        font-size: 16px;
        font-weight: 500;
        line-height: 20px;
        margin-bottom: 20px;
        outline: 0px none transparent;
        -webkit-box-shadow: none;
        -moz-box-shadow: none;
        box-shadow: none;
        resize: none;
    }

    &__span {
        color: rgba(43, 46, 62, 0.45);
        font-family: Gilroy;
        font-size: 12px;
        font-weight: 500;
        line-height: 20px;
    }

    &__btn-wrap {
        display: flex;
        align-items: center;
        gap: 20px;
    }

    &__link {
        color: rgba(43, 46, 62, 0.45);
        text-decoration: underline;
        cursor: pointer;
    }
}

.hystmodal__close {
    top: 20px;
    right: 20px;
    background-image: url("../img/close-btn.svg");
    width: 18px;
    height: 18px;
}

@media (max-width: @mobile-width) {
    .modal {
        padding: 20px 20px;
        border-radius: 15px;
        max-width: @mobile-container;

        &__title {
            font-size: 26px;
            font-weight: 700;
            line-height: 30px
        }

        &__subtitle {
            font-size: 14px;
            font-weight: 400;
            line-height: 20px;
        }

        &__fields {
            flex-direction: column;
            gap: 10px;
            margin-bottom: 10px;
        }

        &__btn-wrap {
            flex-direction: column;
        }

        &__span {
            text-align: center;
        }

        &__input {
            padding: 18px 20px;
            font-size: 14px;
            font-weight: 500;
            line-height: 20px;
        }

        &__comment {
            padding: 18px 20px;
            font-size: 14px;
            font-style: normal;
            font-weight: 500;
            line-height: 20px;
        }

        &__btn {
            padding: 17px 50px 18px 50px;
            font-size: 14px;
            font-weight: 700;
            line-height: 20px; 
            width: 100%;
        }
    }
}