.font-gilroy() {
  font-family: "Gilroy", "Arial", sans-serif;
}

.center-mobile {
  width: @mobile-container;
  margin: 0 auto;
}

.center-tablet {
  width: @tablet-container;
  margin: 0 auto;
}

.center-desktop {
  width: @desktop-container;
  margin: 0 auto;
}

.center-desktop2 {
  width: @desktop-container2;
  margin: 0 auto;
}

.align-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.list-reset {
  padding-left: 0;
  margin: 0;
  list-style-type: none;
}

.btn-reset {
  padding: 0;
  background-color: transparent;
  border: none;
}

.link-reset {
  text-decoration: none;
}

.title {
  margin: 0;
  font-size: 68px;
  line-height: 65px;
  font-weight: 700;
  color: @black;
  letter-spacing: -1.36px;
  .font-gilroy();
}

.span {
  color: @primary;
}

.text {
  margin: 0;
  font-size: 24px;
  font-weight: 500;
  line-height: 35px; 
  .font-gilroy();
}

.tooltip-text {
  font-size: 16px;
  font-weight: 600;
  line-height: 20px;
  .font-gilroy();
}

.btn {
  padding: 12px 40px;
  border-radius: 5px;
  font-size: 14px;
  font-weight: 700;
  line-height: 25px;
  .font-gilroy();
}

.btn-primary {
  color: @white;
  background-color: @primary; 
}

.btn-secondary {
  color: @primary;
  background-color: rgba(0, 155, 255, 0.20);
}

.btn-icon {
  font-size: 16px;
  font-weight: 400;
  line-height: 25px;
  color: @black;
  background-color: none;
  padding-left: 55px;
  .font-gilroy();
  border: 1px dashed #BFC9E0;
}

.phone-text {
  font-size: 18px;
  font-weight: 700;
  line-height: 25px; 
  .font-gilroy();
  color: @black;
}